/* make 12 column  */
.row {
  @include make-row;
}

@include make-grid-columns;

@include make-grid(xs);

/* add background-color to body */

body {
    @include breakpoint($breakpoint-mb) {
        background-color: #f5f5f5;
    }
}


/* add padding to wrapper in screen mobile */

.wrapper {
    @include clearfix;
    @include breakpoint(0, $screen-xs) {
        padding: 0 15px;
    }
}


/* container with in many breakpoint */

.container {
    @include breakpoint($breakpoint-mb) {
        background-color: #fff;
        width: 480px;
        margin: 0 auto;
        @include clearfix;
        &.outer {
            padding: 0 15px;
        }
    }
    @include breakpoint($screen-sm) {
        width: 740px;
    }
    @include breakpoint($screen-md) {
        width: 1010px;
    }
}


/* layout scaffolding */

.sidebar,
.c-column {
    display: none;
}

// define font-size : 0, NOTICE : use with "display: inline-block, vertical-align: top"
// add clearfix to box
.a-column,
.bc-column,
.b-column,
.c-column,
.wide-column,
.highlight,
.sidebar,
.list-article,
.region,
.top-story,
.spotlight {
    @include clearfix;
    font-size: 0;
    width: 100%;
}

.a-column,
.b-column,
.c-column,
.wide-column,
.list-article,
.region,
.top-story,
.spotlight {
    margin-bottom: 10px;
}

@include breakpoint(sm) {
    .a-column {
        width: 380px;
        float: left;
        margin-right: 10px;
    }
    .bc-column {
        width: 320px;
        float: left;
    }
}

@include breakpoint(md) {
    .a-column {
        width: 440px;
    }
    .bc-column {
        width: 530px;
    }
    .b-column {
        width: 360px;
        margin-right: 10px;
        float: left;
    }
    .c-column {
        float: left;
        width: 160px;
        display: block;
    }
    .highlight {
        width: 665px;
        float: left;
        margin-right: 10px;
    }
    .sidebar {
        width: 300px;
        float: left;
        display: block;
    }
}

// region layout
.region {
    &.theme-1,
    &.theme-2 {
        @include clearfix;
        font-size: 0;
        @include breakpoint(sm) {
            & {
                background-color: #F5F5F5;
                width: 355px;
                padding: 0 10px 10px 10px;
            }
        }
    }
}

// top story layout
.top-story {
    @include breakpoint(xs) {
        .column:nth-child(1) {
            .story {
                margin: 0 5px 10px 0;
            }
        }
        .column:nth-child(2) {
            .story:first-child {
                margin: 0 0px 10px 5px;
            }
            .story:nth-child(2) {
                margin: 0 5px 0px 0;
                clear: both;
            }
        }
        .column:nth-child(3) {
            .story:nth-child(2n+1) {
                margin: 0 0px 10px 5px;
            }
            .story:nth-child(2n) {
                margin: 0 5px 10px 0;
                clear: both;
            }
            .story:last-child {
                display: none;
            }
            .story:nth-child(2),
            .story:nth-child(3) {
                margin-bottom: 0;
            }
        }
    }
    @include breakpoint(sm) {
        .column {
            float: left;
            width: 230px;
            margin-right: 10px;
            >.story {
                height: 70px;
                overflow: hidden;
                .thumb {
                    display: none;
                }
            }
        }
        .column:first-child {
            >.story {
                height: 310px;
                .thumb,
                .refer {
                    display: block;
                }
                .story-heading {
                    @include font($font-family-serif, 16, 23, $bold);
                    margin-bottom: 3px;
                }
            }
        }
        .column:nth-child(2) {
            >.story:first-child {
                .thumb,
                .refer {
                    display: block;
                }
                .story-heading {
                    @include font($font-family-serif, 16, 23, $bold);
                    margin-bottom: 3px;
                }
                margin-bottom: 10px;
                height: 230px;
            }
            >.story:last-child {
                margin-bottom: 0px;
            }
        }
        .column:nth-child(3) {
            margin-right: 0;
            .story {
                margin-bottom: 10px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
    @include breakpoint(md) {
        .column {
            float: left;
            width: 215px;
            margin-right: 10px;
            >.story {
                height: 70px;
                overflow: hidden;
                .thumb {
                    display: none;
                }
            }
        }
        .column:first-child {
            >.story {
                height: 310px;
                .thumb,
                .refer {
                    display: block;
                }
                .story-heading {
                    @include font($font-family-serif, 16, 23, $bold);
                    margin-bottom: 3px;
                }
            }
        }
        .column:nth-child(2) {
            >.story:first-child {
                .thumb,
                .refer {
                    display: block;
                }
                .story-heading {
                    @include font($font-family-serif, 16, 23, $bold);
                    margin-bottom: 3px;
                }
                margin-bottom: 10px;
                height: 230px;
            }
            >.story:last-child {
                margin-bottom: 10px;
            }
        }
        .column:nth-child(3) {
            margin-right: 0;
            .story {
                margin-bottom: 10px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}

// spotlight layout
.spotlight {
    @include breakpoint(sm) {
        width: 100%;
        height: 270px;
        overflow: hidden;
    }
}
