/* ====================================
   Footer's style
   ==================================== */

footer#footer {
    @include clearfix;
    margin: 0 -15px;
    padding: 0 15px;
    @include breakpoint(xs){
    	background-color: #f5f5f5;
    }
    .cate {
        border-top: 1px solid #e9e9e9;
        border-bottom: 1px solid #e9e9e9;
        padding: 10px 0;
        @include clearfix;
        >ul {
            @include clearfix;
        }
        .parent {
            float: left;
            >a {
                text-transform: uppercase;
                @include font($font-family-serif, 14, 19, $bold);
                &:hover{
                	font-weight: bold;
                }
            }
            @include breakpoint(xs) {
                width: 50%;
                >a {
                    padding: 0 0 0 20%;
                    @include font($font-family-serif, 14, 40, $normal);
                }
            }
            @include breakpoint(sm) {
                width: 25%;
                >a {
                    @include font($font-family-serif, 14, 40, $normal);
                    display: block;
                }
            }
            @include breakpoint(md) {
                width: 18%;
                >a {
                    @include font($font-family-serif, 14, 30, $normal);
                    display: block;
                }
                &:nth-child(6n){
                	width: 10%;
                }
                &:nth-child(6),
                &:nth-child(10){
                	.child{
                		min-height: 150px;
                	}
                }
                &:nth-child(14),
                &:nth-child(16),
                &:nth-child(18),
                {
                	width: 10%;
                }
            }
        }
        .child {
            display: none;
            @include breakpoint(md) {
            	display: block;
                a {
                    @include font($font-family-serif, 13, 19, $normal);
                    color: #777;
                    line-height: 26px;
                    display: block;
                    &:hover{
                    	color: $link-hover-color;
                    	font-weight: bold;
                    }
                }
            }
        }
    }
    .link {
        display: none;
        @include breakpoint(sm) {
            @include font($font-family-sans-serif, 13, 19, $bold);
            display: block;
            line-height: 40px;
            border-bottom: 1px solid #e9e9e9;
            a {
                display: inline-block;
                margin-right: 20px;
            }
            span {
                color: #DE0202;
            }
        }
    }
    .copyright {
        padding: 15px 0;
        @include font($font-family-sans-serif, 14, 22, $normal);
        color: #777;
        >p {
            margin: 0;
        }
        .powerby {
            font-size: 10px;
        }
        @include breakpoint(xs) {
            text-align: center;
            span.xs-none {
                display: none;
            }
            >p {
                margin: 0;
                span {
                    display: block;
                    clear: both;
                }
            }
        }
        @include breakpoint(sm) {
            padding: 10px 0 15px;
            >p {
                span:before {
                    content: ' - ';
                    display: inline-block;
                    padding: 0 5px;
                }
                span:first-child:before {
                    display: none;
                }
            }
        }
    }
}
