/* ====================================
   Story's style
   ==================================== */

.story {
    @include clearfix;
    //== Normal story
    //
    //## regular story in all box 
    &.theme-summary {
        // MEDIA QUERY : style max width 767
        @include breakpoint(xs) {
            // common style
            .story-heading {
                @include font($font-family-sans-serif, 16, 22, $bold);
                margin: 0;
            }
            //list article
            .list-article & {
                border-bottom: 1px solid #eee;
                padding-bottom: 10px;
                margin-bottom: 7px;
                .story-heading {
                    margin-left: 110px;
                }
                .thumb {
                    @include runaround(left, 3, 10, 0, 0);
                    width: 100px;
                    height: 75px;
                    img {
                        width: 100px;
                        height: 75px;
                    }
                }
                .summary {
                    display: none;
                }
                .refer {
                    display: none;
                }
                &:last-child {
                    border-bottom: none;
                    padding-bottom: 0px;
                    margin-bottom: 0px;
                }
            }
            //box theme 1, top-story 
            .region.theme-1 & {
                width: calc(50% - 5px);
                display: inline-block;
                vertical-align: top;
                margin: 0 5px 10px 0;
                .thumb {
                    width: 100%;
                    height: 0;
                    overflow: hidden;
                    padding-bottom: 59%;
                    margin-bottom: 5px;
                    img {
                        width: 100%;
                        height: auto;
                    }
                }
                .summary {
                    display: none;
                }
                &:nth-child(2n) {
                    margin: 0 0px 10px 5px;
                }
                &:last-child,
                &:nth-last-child(2){
                    margin-bottom: 0;
                }
            }
            //box theme 2
            .region.theme-2 & {
                border-bottom: 1px solid #eee;
                padding-bottom: 10px;
                margin-bottom: 10px;
                .summary {
                    display: none;
                }
                &:last-child {
                    border-bottom: none;
                    padding-bottom: 0;
                    margin-bottom: 0;
                }
            }
            //top-story
            .top-story & {
                width: calc(50% - 5px);
                display: inline-block;
                float: left;
                vertical-align: top;
                .thumb {
                    width: 100%;
                    height: 0;
                    overflow: hidden;
                    padding-bottom: 59%;
                    margin-bottom: 5px;
                    img {
                        width: 100%;
                        height: auto;
                    }
                }
                .summary {
                    display: none;
                }
                .refer {
                    display: none;
                }
            }
            // aside-box,most read
            .aside-160 & ,
            .most-read & ,
            .radio-story &,
            .location-story &
            {
                display: none;
            }
            // 
        }
        // MEDIA QUERY : style min width 768
        @include breakpoint(sm) {
            // common style
            .story-heading {
                @include font($font-family-serif, 13, 19, $bold);
                margin: 0;
            }
            //list article
            .list-article & {
                border-bottom: 1px solid #eee;
                padding-bottom: 10px;
                margin-bottom: 7px;
                .thumb {
                    @include runaround(left, 3, 10, 5, 0);
                    width: 215px;
                    height: 130px;
                    img {
                        width: 215px;
                        height: 130px;
                    }
                }
                .story-heading {
                    @include font($font-family-serif, 16, 23, $bold);
                    margin-bottom: 3px;
                }
                .summary {
                    @include font($font-family-sans-serif, 13, 19, $normal);
                    color: $gray-light;
                    margin-bottom: 5px;
                }
                &:last-child {
                    border-bottom: none;
                    padding-bottom: 0px;
                    margin-bottom: 0px;
                }
                .refer {
                    margin: 0;
                    padding: 0;
                    li {
                        margin-bottom: 2px;
                        .refer-heading {
                            @include font($font-family-serif, 12, 17, $normal);
                            margin: 0;
                            a {
                                &:before {
                                    content: "";
                                    font-size: 10px;
                                    color: #E64C3C;
                                    padding-right: 5px;
                                }
                            }
                            .timestamp {
                                color: $gray-light;
                                display: none;
                            }
                        }
                    } // end li
                } // end refer
            }
            //box theme 1
            .region.theme-1 & {
                width: 160px;
                margin: 0px 5px 10px 0px;
                display: inline-block;
                vertical-align: top;
                .thumb {
                    width: 160px;
                    height: 100px;
                    margin-bottom: 5px;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
                .summary {
                    display: none;
                }
                &:nth-child(2n) {
                    margin: 0px 0px 10px 5px;
                }
                &:last-child,
                &:nth-last-child(2){
                    margin-bottom: 0;
                }
            }
            //box theme 1 : has feature in html
            .region.has-feature.theme-1 & {
                float: left;
                &:nth-child(2n + 1) {
                    margin: 0px 0px 10px 5px;
                }
                &:nth-child(2n) {
                    margin: 0px 5px 10px 0px;
                    clear: both;
                }
                &:nth-child(4) {
                    display: none;
                }
                &:nth-child(2),
                &:nth-child(3),
                {
                    margin-bottom: 0;
                }
            }
            //box theme 2
            .region.theme-2 & {
                margin-bottom: 7px;
                .story-heading {
                    @include font($font-family-serif, 13, 19, $normal);
                    margin: 0;
                    &:before {
                        content: "";
                        width: 4px;
                        height: 6px;
                        background: url(../img/icons/bullet-1.png) 0 0 no-repeat;
                        margin-right: 5px;
                        display: inline-block;
                        vertical-align: top;
                        margin-top: 6px;
                    }
                }
                .summary {
                    display: none;
                }
                &:last-child,
                {
                    margin-bottom: 0;
                }
            }
            .top-story & {
                background-color: #f5f5f5;
                border-bottom: 1px solid #ddd;
                padding: 5px 5px 10px;
                .thumb {
                    width: 100%;
                    height: 0px;
                    padding-bottom: 60%;
                    overflow: hidden;
                    margin-bottom: 5px;
                    img {
                        width: 100%;
                        height: auto;
                    }
                }
                .summary {
                    display: none;
                }
                .refer {
                    display: none;
                    margin: 0;
                    padding: 0;
                    li {
                        margin-bottom: 5px;
                        .refer-heading {
                            @include font($font-family-serif, 12, 17, $normal);
                            margin: 0;
                            max-height: 34px;
                            overflow: hidden;
                            a {
                                &:before {
                                    content: "";
                                    font-size: 10px;
                                    color: #E64C3C;
                                    padding-right: 5px;
                                }
                            }
                            .timestamp {
                                color: $gray-light;
                                display: none;
                            }
                        }
                    } // end li
                } // end refer
            }
            // aside-box
            .aside-160 & {
                margin-bottom: 7px;
                padding-bottom: 10px;
                border-bottom: 1px solid #eee;
                .story-heading {
                    @include font($font-family-serif, 12, 17, $normal);
                    margin: 0;
                }
                .summary {
                    display: none;
                }
                &:last-child {
                    margin-bottom: 0px;
                    padding-bottom: 0px;
                    border-bottom: none;
                }
            }
            // most read
            .most-read & {
                margin-bottom: 10px;
                .story-heading {
                    @include font($font-family-serif, 12, 17, $normal);
                    margin: 0;
                    padding-top: 0px;
                    margin-left: 28px;
                }
                &:before {
                    content: counter(my-number);
                    counter-increment: my-number;
                    display: block;
                    float: left;
                    margin-right: 10px;
                    font-size: 50px;
                    @include font($font-family-serif, 30, 30, $bold);
                    color: #999;
                }
            }
            // radio box,location box
            .radio-story &,
            .location-story & {
                margin-bottom: 10px;
                .story-heading {
                    @include font($font-family-serif, 13, 20, $bold);
                    margin: 0;
                    padding-top: 0px;
                    padding-left: 20px;
                    position: relative;
                    &:before {
                        display: inline-block;
                        font: normal normal normal 14px/1 FontAwesome;
                        font-size: inherit;
                        text-rendering: auto;
                        -webkit-font-smoothing: antialiased;
                        font-size: 14px;
                        position: absolute;
                        left: 0;
                        top: 3px;
                        color: $gray;
                    }
                }
            }
            .radio-story & {
                .story-heading {
                    &:before {
                        content: "";
                    }
                }
            }
            .location-story & {
                .story-heading {
                    &:before {
                        content: "";
                        font-size: 16px;
                        top: 0;
                        left: 2px;
                    }
                }
            }
        }
        // MEDIA QUERY : style min width 768
        @include breakpoint(md) {
            .top-story & {
                .thumb {
                    width: 215px;
                    height: 130px;
                    margin: -5px -5px 5px -5px;
                    padding-bottom: 0;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
    //== Feature story
    //
    //## highlight story in all box 
    &.theme-feature {
        // MEDIA QUERY : style max width 767
        @include breakpoint(xs) {
            //box theme 1
            .region.theme-1 & {
                width: 100%;
                margin: 0px;
                display: block;
                vertical-align: inherit;
                .story-heading {
                    @include font($font-family-sans-serif, 20, 24, $bold);
                    margin-top: 0;
                }
                .thumb {
                    width: 100%;
                    height: 0;
                    overflow: hidden;
                    padding-bottom: 59%;
                    margin-bottom: 5px;
                    img {
                        width: 100%;
                        height: auto;
                    }
                }
                .summary {
                    @include font($font-family-serif, 12, 15);
                    display: none;
                }
            }
            //box theme 2
            .region.theme-2 & {
                .story-heading {
                    @include font($font-family-sans-serif, 20, 24, $bold);
                    margin-top: 0;
                }
                .thumb {
                    @include runaround(right, 3, 0, 10, 10);
                    width: 40%;
                    height: 0;
                    overflow: hidden;
                    padding-bottom: 23%;
                    img {
                        width: 100%;
                        height: auto;
                    }
                }
                .summary {
                    display: none;
                }
            }
            //spotlight-story
            .spotlight & {
                width: 100%;
                margin: 0px;
                display: block;
                vertical-align: inherit;
                .story-heading {
                    @include font($font-family-sans-serif, 22, 26, $bold);
                    margin-top: 0;
                    margin-bottom: 5px;
                }
                .thumb {
                    height: 0;
                    overflow: hidden;
                    padding-bottom: 62%;
                    margin-bottom: 5px;
                    margin: 0 -15px 5px -15px;
                    img {
                        width: 100%;
                        height: auto;
                    }
                }
                .summary {
                    @include font($font-family-sans-serif, 14, 19);
                    color: $gray;
                }
            }
            // aside-box
            .aside-160 & {
                display: none;
            }
        }
        // MEDIA QUERY : style min width 768
        @include breakpoint(sm) {
            //box theme 1
            .region.theme-1 & {
                width: 160px;
                margin: 0px 5px 10px 0px;
                display: inline-block;
                vertical-align: top;
                float: left;
                .story-heading {
                    @include font($font-family-serif, 13, 19, $bold);
                    margin: 0;
                }
                .thumb {
                    width: 160px;
                    height: 100px;
                    margin-bottom: 5px;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
                .summary {
                    display: none;
                }
                &:nth-child(2n) {
                    margin: 0px 0px 10px 15px;
                }
            }
            //box theme 1
            .region.theme-2 & {
                .story-heading {
                    @include font($font-family-serif, 16, 23, $bold);
                    color: $gray-base;
                    margin-top: 0;
                    margin-bottom: 7px;
                }
                .thumb {
                    width: 335px;
                    height: 205px;
                    margin-bottom: 7px;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
            //spotlight-story
            .spotlight & {
                background-color: #f5f5f5;
                .thumb {
                    @include runaround(right, 0, 0, 0, 0);
                    width: 440px;
                    height: 270px;
                    margin-left: 10px;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
                .story-heading {
                    @include font($font-family-serif, 25, 32, $bold);
                    margin: 0;
                    padding: 5px;
                    padding-right: 0;
                }
                .summary {
                    @include font($font-family-sans-serif, 13, 19);
                    color: $gray-light;
                    padding: 0 5px;
                    padding-right: 0;
                    max-height: 114px;
                    overflow: hidden;
                }
            }
            // aside box
            .aside-160 & {
                margin-bottom: 7px;
                padding-bottom: 10px;
                border-bottom: 1px solid #eee;
                .story-heading {
                    @include font($font-family-serif, 12, 17, $bold);
                    margin-top: 0;
                    margin-bottom: 0px;
                }
                .thumb {
                    width: 160px;
                    height: 100px;
                    margin-bottom: 7px;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
}

// story-link is used as story parent when the
// entire story block is to be clickable
.story-link {
    @include clearfix;
    display: block;
    @include breakpoint(sm) {
        .region.theme-2 .story.theme-feature &:hover .story-heading {
            color: $link-hover-color;
        }
    }
}
