/* ====================================
   Header's style
   ==================================== */

// mobile header
.header-mb {
    margin: 0 0px 10px;
    height: 50px;
    position: relative;
    background-color: #E53B19;
    @include breakpoint(0, $screen-xs) {
        margin: 0 -15px;
    }
    >.container {
        position: relative;
        background-color: inherit;
    }
    .hour-btn,
    .menu-btn {
        display: block;
        position: absolute;
        width: 34px;
        height: 34px;
        border: 1px solid #EEEEEE;
        background-color: #E53B19;
        top: 8px;
        border-radius: 50%;
        text-align: center;
        i {
            color: #fff;
        }
    }
    .menu-btn {
        line-height: 30px;
        left: 10px;
    }
    .hour-btn {
        line-height: 34px;
        right: 10px;
        font-size: 11px;
        text-transform: uppercase;
        color: #fff;
    }
    .logo {
        line-height: 50px;
        h2 {
            text-align: center;
            margin: 0;
            padding: 0;
            color: #fff;
        }
    }
}

// mobile menu
.nav-mobile {
    background-color: #f5f5f5;
    // width: calc(100% - 55px);
    box-shadow: 2px 0 5px 0 rgba(0, 0, 0, 0.25);
    >.title {
        height: 50px;
        background-color: #e5e5e5;
        padding: 0 10px;
        line-height: 50px;
        font-size: 16px;
        text-transform: uppercase;
        margin: 0;
    }
    >.action {
        border-bottom: 1px solid #eee;
        padding: 10px;
        .search {
            border: 1px solid #eee;
            background-color: #fff;
            height: 40px;
            padding: 0 10px;
            width: 100%;
        }
    }
    nav {
        ul {
            .parent {
                border-bottom: 1px solid #eee;
                position: relative;
                >a.cate-name {
                    @include font($font-family-serif, 16, 50, $bold);
                    position: relative;
                    z-index: 10;
                    display: block;
                    height: 50px;
                    color: #777;
                    text-transform: uppercase;
                    padding: 0 10px;
                    i {
                        font-size: 18px;
                        float: left;
                        padding-top: 16px;
                    }
                    &:hover,
                    &:focus,
                    &:active,
                    {
                        color: #000;
                    }
                    span {
                        margin-left: 35px;
                        display: block;
                    }
                }
                a.btn-action {
                    position: absolute;
                    right: 0;
                    top: 0px;
                    width: 50px;
                    height: 50px;
                    z-index: 30;
                    &:before {
                        content: "";
                        padding-top: 13px;
                        text-align: center;
                        display: block;
                        font: normal normal normal 14px/1 FontAwesome;
                        text-rendering: auto;
                        -webkit-font-smoothing: antialiased;
                        font-size: 22px;
                        color: #999;
                        font-size: 20px;
                    }
                    &.collapsed {
                        &:before {
                            content: "";
                        }
                    }
                }
                &.link {
                    margin-bottom: 1px;
                    background-color: #eee;
                    &:after {
                        position: absolute;
                        right: 0;
                        top: 0px;
                        width: 50px;
                        height: 50px;
                        z-index: 0;
                        content: "";
                        padding-top: 23px;
                        text-align: center;
                        display: block;
                        font: normal normal normal 14px/1 FontAwesome;
                        text-rendering: auto;
                        -webkit-font-smoothing: antialiased;
                        font-size: 22px;
                        color: #999;
                        font-size: 5px;
                    }
                }
            }
            .child {
                position: relative;
                z-index: 20;
                padding-bottom: 5px;
                li>a {
                    display: block;
                    height: 35px;
                    line-height: 35px;
                    padding-left: 55px;
                    color: #777;
                }
            }
        }
    }
}

// web header 
header#header {
    display: none;
    @include breakpoint(sm) {
        display: block;
    }
    @include clearfix;
    @include font($font-family-sans-serif, 12, 16, $normal);
    background-color: #fff;
    .topbar {
        height: 30px;
        background-color: #f5f5f5;
        border-bottom: 1px solid #ddd;
        padding-top: 5px;
        .container {
            background-color: inherit;
        }
        time {
            width: 40%;
            float: left;
            line-height: 20px;
        }
        .stuff {
            text-align: right;
            width: 60%;
            float: left;
            .search,
            .link,
            .top-social {
                display: inline-block;
            }
            .search {
                margin-right: 10px;
                i {
                    font-size: 18px;
                }
            }
            .link {
                margin: 0;
                margin-right: 10px;
                a {
                    &:after {
                        content: "|";
                        display: inline-block;
                        padding: 0 5px;
                        color: #999;
                    }
                    &:last-child {
                        &:after {
                            display: none;
                        }
                    }
                }
            }
            .top-social {
                font-size: 0;
                vertical-align: top;
                a {
                    margin-right: 5px;
                    &:last-child {
                        margin-right: 0;
                    }
                }
            }
        }
    }
    >.container {
        position: relative;
        height: 110px;
        @include breakpoint($screen-sm, $screen-sm-max) {
            line-height: 110px;
            text-align: center;
        }
        @include breakpoint(md) {
            .logo {
                position: absolute;
                top: 15px;
                left: 0;
            }
        }
        .fyi-728x90 {
            display: none;
            @include breakpoint(md) {
                display: block;
                position: absolute;
                top: 10px;
                right: 0;
            }
        }
    }
}
